import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";

const routes: Routes = [
    {
        path: "rfid",
        loadChildren: () =>
            import("./rfid/rfid.module").then((m) => m.RfidModule),
        data: { title: "RFID" },
    },
    {
        path: "cpo-rfid",
        loadChildren: () =>
            import("./cpo-rfid/cpo-rfid.module").then((m) => m.CpoRfidModule),
        data: { title: "On Board" },
    },
    {
        path: "rfid-recharge",
        loadChildren: () => import("./rfid-recharge/rfid-recharge.module").then((m)=> m.RfidRechargeModule),
        data : {
            title : "RFID Recharge"
        }
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],

})
export class RfidManagementRoutingModule {}
